.wrapper {
  width: 33%;
}

.card {
  margin: 5px;
  padding: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 2px solid #26323a;
  border-radius: 15px;
  transition: opacity 0.3s linear;
}

.card img {
  width: 70px;
}

.card:hover {
  opacity: 0.8;
}
